*
{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.home
{
    width: 100%;
}
.home .top_banner
{
    max-width: 100%;
    height: 395px;
    background: url('http://localhost:3000/image/slide-1.webp');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px 60px;
}
.home .top_banner .contant
{
    width: 100%;
    position: relative;
    top: 20%;
    padding: 10px 50px;
}
.home .top_banner .contant h3
{
    text-transform: capitalize;
    color: #cd1e76;
    font-weight: 300;
    letter-spacing: 1px;
}
.home .top_banner .contant h2
{
    text-transform: uppercase;
    color: #232323;
    letter-spacing: 1px;
    font-size: 42px;
    margin-top: 10px;
}
.home .top_banner .contant p
{
    color: #232323;
    letter-spacing: 1px;
    margin-top: 10px;
    margin-bottom: 30px;
}
.home .top_banner .contant .link
{
    padding: 10px 30px;
    background: #fed700;
    color: #232323;
    text-decoration: none;
    border-radius: 5px;
    transition: 0.5s;
}
.home .top_banner .contant .link:hover
{
    color: #fed700;
    background: #cd1e76;
}
